@import "./styles/base";
@import "./styles/mixins";

.bg {
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
        url(./images/bg.jpg);
    background-size: cover, cover;

    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}

.app {
    position: relative;
    min-height: 100vh;
}

.container {
    margin: 0 auto;
    width: 90%;

    @media screen and (min-width: 1200px) {
        width: 70%;
    }

    padding-bottom: 15rem;
}
